
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#app header {
  height: 45vh;
  background-color: #00bcd4;
  color: blue;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: -webkit-gradient(linear, left top, left bottom, from(#0097a7), to(#00bcd4));
  background: linear-gradient(#0097a7, #00bcd4);
  background-size: 250% 250%;
  -webkit-animation: kKvztC 10s ease infinite;
          animation: kKvztC 10s ease infinite;
  font-weight: 400;
  -webkit-transition: height 250ms ease-in-out;
  transition: height 250ms ease-in-out;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
#app header h1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 700;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transition: font-size 250ms ease-in-out, padding 150ms ease-in;
  transition: font-size 250ms ease-in-out, padding 150ms ease-in;
  background-color: #002635;
  color: white;
  padding: 0.5rem 1rem;
  margin: 0;
  width: auto;
  -webkit-user-select: text;
     -moz-user-select: text;
      -ms-user-select: text;
          user-select: text;
}
@media only screen and (min-width: 375px) {
#app header h1 {
    font-size: 2rem;
}
}
@media only screen and (min-width: 768px) {
#app header h1 {
    font-size: 3.5rem;
    padding: 1rem 2rem;
}
}
#app main {
  position: absolute;
  top: 40vh;
  right: 0;
  left: 0;
  margin: 0 auto;
  max-width: 840px;
  z-index: 2;
}
#particles-js{
  position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width:100%;
    height:100%;
}

.section {
  position: relative;
  width: 100%;
  background-color: white;
  -webkit-box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
  background-color: white;
  outline: 1px solid rgba(0, 0, 0, 0.125);
  box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.2);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-bottom: 1.5rem;
:last-child {
    border-bottom-width: 0;
}
}
.tool-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  z-index: 1;
  background: hsl(166, 90%, 20%);
  color: white;
  position: relative;
  -webkit-transition: background-color 125ms ease-in-out;
  transition: background-color 125ms ease-in-out;
  padding-top: 1.125rem;
  padding-bottom: 1.125rem;
}
@media only screen and (min-width: 768px) {
.section {
    margin-bottom: 3rem;
    padding-bottom: 3rem;
}
}
.section-content {
  max-width: 100%;
  padding: 1.125rem 1.5rem;
}
@media only screen and (min-width: 768px) {
  padding: 1.5rem 3rem;
  padding-top: 1.5rem;
}
.split-bar {
  border: 0;
  width: 75%;
  margin: 0.75rem auto;
  border-bottom: 1px solid #eee;
}
.content-title {
  font-size: 1.25rem;
  line-height: 1.5rem;
  text-align: center;
  margin-bottom: 0;
}
@media only screen and (min-width: 768px) {
.content-title {
    font-size: 1.5rem;
    line-height: 2.25rem;
}
}
.read-more {
  display: inline-block;
  background-color: white;
  padding: 0.5rem 1.5rem;
  font-size: 1.125rem;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  border-width: 2px;
  border-style: solid;
  -webkit-transition: all 125ms ease-in-out;
  transition: all 125ms ease-in-out;
  font-family: sans-serif;
  background-color: hsl(166, 90%, 20%);
  color: white;
  border-color: hsl(166, 90%, 20%);
}
body {
  margin: 0;
  padding: 0;
  background-color: #002635;
}

hr {
  margin-left: 0;
  margin-right: 0;
  margin-top: 0;
  padding-bottom: 0;
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
  margin-bottom: calc(1.5rem - 1px);
  background: hsla(0, 0%, 0%, 0.2);
  border: none;
  height: 1px;
}

h1 {
  margin-left: 0;
  margin-right: 0;
  margin-top: 0;
  padding-bottom: 0;
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
  margin-bottom: 1.5rem;
  color: inherit;
  font-family: -apple-system, 'BlinkMacSystemFont', 'Segoe UI', 'Roboto',
    'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-weight: bold;
  text-rendering: optimizeLegibility;
  font-size: 2.25rem;
  line-height: 1.1;
}
